import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
    state = {
        isLoading: true,
        movies: []
    };

    // async : 비동기 -> 너는 기다려야만 하는 함수 ( 기다려야 해! )
    getMovies = async () => {
        //data fetch : 데이터를 가져오다
        //axios : fetch 위에 있는 작은 layer
        //await은 axios 가 끝날때 까지 기다림 
        const { data: { data: { movies } } } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");

        //{ data: { movies } } } == movies.data.data.movies
        // this.setState({movies : movies}); 이렇게 써야하지만 아래처럼 써도 작동한다 왜냐? javascript는 똑똑하니까
        this.setState({ movies, isLoading: false }); // 위에서 await를 걸어뒀기 때문에 데이터가 다 받아져야만 set이 실행된다
    }

    componentDidMount() {
        this.getMovies();
    };

    render() {
        // js 의 es6
        const { isLoading, movies } = this.state;
        // class 라고 쓰면 javscript 가 진짜 class와 혼란스러워함
        return <section className="container">{isLoading ? (
            <div className="loader">
                <span className="loader__text">Loading...</span>
            </div>
        ) : (
                <div className="movies">
                    {movies.map(movie => (
                        <Movie
                            key={movie.id}
                            id={movie.id}
                            year={movie.year}
                            title={movie.title}
                            summary={movie.summary}
                            poster={movie.medium_cover_image}
                            genres={movie.genres} />
                    ))}
                </div>
            )}</section>
    }

}

export default App;

